

















import { Component, Prop, PropSync, Emit, Vue } from 'vue-property-decorator';
import { CaptainsLogQuestionArray } from '../model/CaptainsLogClasses';

@Component
export default class CaptainsLogQuestionComponent extends Vue {
    @Prop() questionArray!: CaptainsLogQuestionArray;

    @Emit('captainLogUpdated')
    private captainLogUpdated(): void {}

    private isEditMode: boolean[] = [false, false, false, false, false, false];

    private onEditClicked(index: number): void {
        this.isEditMode[index] = !this.isEditMode[index];
        this.$forceUpdate();
    }
    private onSaveClicked(index: number): void {
        this.isEditMode[index] = !this.isEditMode[index];
        this.captainLogUpdated();
        this.$forceUpdate();
    }
}
