












import { Component, Vue } from 'vue-property-decorator';
import CaptainsLogQuestionComponent from '@/components/CaptainsLogQuestionComponent.vue';
import { CaptainsLogClass, CaptainsLogQuestionArray } from '@/model/CaptainsLogClasses';
import MyDatePicker from '../components/MyDatePicker.vue';

@Component({
  components: {
      'my-date-picker': MyDatePicker,
      CaptainsLogQuestionComponent
  },
})
export default class CaptainsLog extends Vue {
    private myInput: string = '';
    private created(): void {};
    private curDate: string = (new Date()).toDateString();

    get captainsLog(): CaptainsLogClass
    {
        return this.$store.state.captainsLog;
    }

    get currentCaptainsLog(): CaptainsLogQuestionArray {
        return this.captainsLog.getCaptainLogByDate(this.curDate);
    }

    public get dateClass(): {[index: string]: string}
    {
        let output: {[index: string]: string} = {};
        this.captainsLog.partialDates.forEach((elem) => {
        output[elem] = 'partialDate';
        });
        this.captainsLog.fullDates.forEach((elem) => {
        output[elem] = 'fullDate';
        });
        return output;
    }

    private onCaptainsLogUpdated()
    {
        this.captainsLog.commitChanges();
    }

    private copyToClipboardClicked()
    {
        var el = document.createElement('textarea');
        document.body.appendChild(el);
        el.value = this.currentCaptainsLog.clipboardContent;
        el.select();
        document.execCommand('copy');
        document.body.removeChild(el);

        document.dispatchEvent(new CustomEvent('show-toaster', { detail: 'Data copied to clipboard' }));
    }
}
